.form-grid {
  grid-template-columns: repeat(12, 1fr);
}
.form-grid > *:first-child {
  grid-column: 1/6;
}
.form-grid > *:nth-child(2) {
  grid-column: 6/8;
}
.form-grid > *:last-child {
  grid-column: 8/13;
}
@media (max-width: 1300px) {
  .form-grid {
    display: flex !important;
    gap: 3rem;
    flex-direction: column;
  }
}
@media (max-width: 768px) {
  .form-grid h2 {
    font-size: 1.125rem !important;
  }
}

.form__or {
  background: var(--bs-white);
  box-shadow: 0px 0px 45.9px rgba(255, 255, 255, 0.25);
  color: var(--bs-black);
  font-weight: 500;
}

input[type=checkbox] {
  appearance: none;
  min-width: 22px;
  min-height: 22px;
  border: 3px solid var(--bs-primary);
  border-radius: 6px;
  display: inline-block;
  position: relative;
  transition: background 0.3s ease;
  overflow: hidden;
  background: transparent;
  cursor: pointer;
}
input[type=checkbox]:checked {
  background: var(--bs-white);
}
input[type=checkbox]:checked:before {
  opacity: 1;
}
input[type=checkbox]:before {
  content: "";
  width: 15px;
  height: 15px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url("data:image/svg+xml;base64,PHN2ZyB2aWV3Qm94PSIwIDAgMTUgMTIiIGZpbGw9Im5vbmUiIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyI+DQo8cGF0aCBkPSJNMSA0Ljg1NzE0TDUuNzI3MjcgMTBMMTQgMSIgc3Ryb2tlPSIjNDY0MkYzIiBzdHJva2Utd2lkdGg9IjIiLz4NCjwvc3ZnPg0K");
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 6px;
  opacity: 0;
  transition: opacity 0.3 ease;
}

.main-form {
  border: 2px solid rgba(255, 255, 255, 0.05);
}
.main-form:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0 !important;
  width: 100% !important;
  height: 100% !important;
  background: rgba(255, 255, 255, 0.04);
  z-index: -1;
}
.main-form__button {
  background: linear-gradient(90deg, #4642f3, #3431c6);
  background-size: 300% 300%;
  transition: box-shadow 0.3s ease;
  box-shadow: none;
}
.main-form__button:hover {
  animation: gradientAnimation 0.8s forwards;
  box-shadow: 0px 4px 16px 0px rgba(70, 66, 243, 0.25);
}
.main-form__button:not(:hover) {
  animation: gradientAnimationReverse 0.8s forwards;
  box-shadow: none;
}
.main-form__description {
  line-height: 1.2;
}
@media (max-width: 768px) {
  .main-form__description {
    font-size: 1rem !important;
  }
}
.main-form__label {
  font-size: 1rem;
}
.main-form__input {
  outline: none;
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(70, 66, 243, 0.2);
  color: var(--bs-white);
}
.main-form__input:placeholder {
  color: rgba(255, 255, 255, 0.24);
}
.main-form__checkbox {
  font-size: 1rem;
}
.main-form__checkbox a {
  text-decoration: underline;
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}
@keyframes gradientAnimationReverse {
  0% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.item-network {
